html, body {
    padding: 0;
    margin: 0;
    background-color: #282c34;
}
.home{
    background-color: #282c34;
}
.divider{
    width:50px;
    height:auto;
    display:inline-block;
}

.table1{
font-family: arial, sans-serif;
border-collapse: collapse;
width: 100%;
font-size: 14px
}

.td1, .th1 {
border: 1px solid  rgb(255, 255, 255, .25);
text-align: left;
padding: 8px;
color: white;
border-left-width: 0px;
border-right-width: 0px;
}

th.th1{
    border: 0px solid #dddddd;
}

.tr1:nth-child(even) {
background-color: #dddddd;
}

a.title{
    color: white;
}

.row {
display: flex;
}

/* Create four equal columns that sits next to each other */
.column {
flex: 25%;
max-width: 19%;
flex-shrink: 1;
}

.img-item{
width: 200px;
height: 200px;
margin-bottom: 30px;
}

.albums{
width: 170px;
height: 170px;
margin: 0px;
}

.details h3{
margin: 1px;
margin-top: 5px;
color: white;
font-weight: normal;
font-size: small;
}

td.td1{
    border: 1px transparent
}

.details p{
margin: 1px;
color: grey;
font-size: small;
}
/*  */
.menu{
    display: flex;
    margin: 10px;
    flex-direction:row;
    gap: 3%;
}

.web_player{
    position: fixed;
    bottom: 0;
    width: 100%;
}

.play_btn{
    background-color: transparent;
    border: none;
    color: white;
}

